import React, { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';
import "./ContactUs.css"
import Swal from 'sweetalert2';
import 'sweetalert2/dist/sweetalert2.css';


export const ContactUs = () => {
  const form = useRef();
  const [name,setName]=useState("")
  const [email,setEmail]=useState("")
  const [text,setText]=useState("")

  const sendEmail = (e) => {
    e.preventDefault();
   
    if(name&&email&&text){
      emailjs.sendForm('service_odkfz3m', 'template_6wncikl', form.current, 'XlVOJc53m86MsNE2N')
      .then((result) => {
          console.log(result.text);
          Swal.fire({
            title: 'Email Sended',
            icon: 'success',
            text: 'Your message has been successfully sended!',
          });
          setName("")
          setEmail("")
          setText("")
      }, (error) => {
          console.log(error.text);
      });
    }else if(!name){
      Swal.fire({
        title: 'Warning',
        icon: 'warning',
        text: 'You have to enter name',
      });
    }else if(!email){
      Swal.fire({
        title: 'Warning',
        icon: 'warning',
        text: 'You have to enter Email',
      });
    }else if(!text){
      Swal.fire({
        title: 'Warning',
        icon: 'warning',
        text: 'You have to enter Massage',
      });
    }
    
  };

  return (
    <form ref={form} onSubmit={sendEmail}>
    <h3>Send Message me Here</h3>
      <label>Name</label>
      <input value={name} onChange={(e)=>setName(e.target.value)} type="text" name="user_name" />
      <label>Email</label>
      <input value={email} onChange={(e)=>setEmail(e.target.value)} type="email" name="user_email" />
      <label>Message</label>
      <textarea value={text} onChange={(e)=>setText(e.target.value)} name="message" />
      <input type="submit" value="Send" />
    </form>
  );
};